import React from 'react';
import {Box, Grid, makeStyles} from '@material-ui/core';
import MainBg from '../img/mainBg.jpg';

const useStyles = makeStyles({
    MainBg: {
        height: '100vh',
        width: '100%',
        // backgroundImage: `url(${MainBg})`,
        background: `linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) ), url(${MainBg}) center;`,
        // backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
    },
    MainText: {
        maxWidth: '600px',
        border: '2px solid #fff',
        padding: '50px 20px',
        color: '#ffffff',
        fontSize: '45px',
        textAlign: 'center',
        fontFamily: 'Poiret One, cursive',
    }
    
});


function App() {
    const classes = useStyles();
    return(
        <React.Fragment>
            <Box classes={{root: classes.MainBg}}>
                <Grid container alignItems={'center'} justify={'center'} style={{height: '100%'}}>
                    <Grid item>
                        <Box classes={{root: classes.MainText}} mx={{xs: 2, md: 'auto'}}>
                            <Box fontSize='56px'>Андриан и Ирина</Box>
                            <Box fontSize='28px' mt={'20px'}>Наша свадьба состоится 24 апреля 2021 года</Box>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
            <Box bgcolor={'#B1B882'} py={4} color={'#fff'} fontFamily={'Poiret One, cursive'}>
                <Box textAlign={'center'} fontSize={'35px'} >Свадьба начнется через</Box>
                <Box mt={'30px'} mx={'auto'} textAlign={'center'} maxWidth={'800px'}>
                    <Grid container justify={'space-between'}>
                        <Grid item>
                            <Box fontSize={'24px'}>Дней</Box>
                            <Box mt={'10px'}>20</Box>
                        </Grid>
                        <Grid item>
                            <Box fontSize={'24px'}>Часов</Box>
                            <Box mt={'10px'}>10</Box>
                        </Grid>
                        <Grid item>
                            <Box fontSize={'24px'}>Минут</Box>
                            <Box mt={'10px'}>5</Box>
                        </Grid>
                        <Grid item>
                            <Box fontSize={'24px'}>Секунд</Box>
                            <Box mt={'10px'}>5</Box>
                        </Grid>
                    </Grid>
                </Box>
            </Box>
        </React.Fragment>
    );
}

export default App;
